:root {
    --green-000: #001000;
    --green-100: #202220;
    --green-500: #1a9940;
    --green-900: #9beeb4;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background: var(--green-100);
}

#root {
    display: flex;
    justify-content: center;
}

.portfolio-card {
    border-radius: 8px;
    box-shadow: 0 0 16px var(--green-000);
    display: flex;
    height: fit-content;
    margin: 16px;
    max-width: 900px;
    min-width: 700px;
    overflow: hidden;
    position: relative;
}

.portfolio-card > .image-area {
    max-height: 256px;
    position: relative;
}

.portfolio-card > .image-area > img {
    max-height: 256px;
}

.portfolio-card > .image-area > h1 {
    color: var(--green-100);
    left: 12px;
    position: absolute;
    text-shadow:
    1px 1px var(--green-900),
    0 0 4px var(--green-900);
    top: 2px;
}

.portfolio-card > .text-area {
    background: var(--green-100);
    padding: 2px 12px;
    width: 100%;
}

.portfolio-card > .text-area > h1 {
    border-bottom: 1px solid var(--green-500);
    color: var(--green-500);
    margin-bottom: 8px;
    text-shadow:
    1px 1px var(--green-000),
    0 0 4px var(--green-000);
}

.portfolio-card > .text-area > .paragraph-container {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.portfolio-card p {
    color: var(--green-900);
}

.under-construction {
    display: flex;
    flex-direction: column;
}

.under-construction > h1, .under-construction > h3 {
    color: var(--green-500);
    margin-bottom: 32px;
    text-align: center;
    text-shadow:
    1px 1px var(--green-000),
    0 0 4px var(--green-000);
    width: 100;
}

.under-construction > div {
    display: flex;
    gap: 32px;
    justify-content: center;
}

.under-construction a {
    border: 1px solid var(--green-000);
    border-radius: 8px;
    box-shadow: 0 0 8px var(--green-000);
    color: var(--green-900);
    padding: 8px 16px;
    text-decoration: none;
}